<template>
  <div class="header">
    <Row>
      <!-- <i-col class="center" span="1">
        <div class="m_action_item">
          <Tooltip content="">
            <span class="iconfont icon-tupian"></span>
          </Tooltip>
        </div>
      </i-col> -->
      <i-col v-for="(item,index) in actionBtnList" :key="index" class="center" span="1">
        <div @click="insert(item.template, item.isSpecial)" class="m_action_item">
          <Tooltip :content="item.desc">
            <span class="iconfont" :class="item.icon"></span>
          </Tooltip>
        </div>
      </i-col>

      <i-col class="center" span="1">
        <Button @click="makeFile" type="primary"> markdown</Button>
      </i-col>
    </Row>
  </div>
</template>
<script>
import bus from './../lib/bus.js';
export default {
  name: 'Header',
  data () {
    return {
      linkText: '', // 
      linkAddress: '', // 
      actionBtnList: [ // 
        {
          desc: '',
          template: ' **text** ',
          icon: 'icon-jiacu'
        }, {
          desc: '',
          template: ' *text* ',
          icon: 'icon-qingxie'
        }, {
          desc: '',
          template: ' ~~text~~ ',
          icon: 'icon-shanchuxian'
        }, {
          desc: '',
          template: 'link',
          isSpecial: true, // template
          icon: 'icon-link'
        }, {
          desc: '',
          template: ` ==text== `,
          icon: 'icon-biaozhu'
        }, {
          desc: '',
          template: `
# text`,
          icon: 'icon-h1'
        }, {
          desc: '',
          template: `
## text`,
          icon: 'icon-h2'
        }, {
          desc: '',
          template: `
### text`,
          icon: 'icon-h3'
        }, {
          desc: '',
          template: `
#### text`,
          icon: 'icon-h4'
        }, {
          desc: '',
          template: `
##### text`,
          icon: 'icon-h5'
        }, {
          desc: '',
          template: `
###### text`,
          icon: 'icon-h6'
        }, {
          desc: '',
          template: `
---`,
          icon: 'icon-fengexian'
        },
        {
          desc: '',
          template: `
> text`,
          icon: 'icon-yinyong'
        }, {
          desc: '',
          template: `
- text - 1
- text - 2`,
          icon: 'icon-wuxuliebiao'
        }, {
          desc: '',
          template: `
1. text - 1
1. text - 2 `,
          icon: 'icon-youxuliebiao'
        }, {
          desc: '',
          template: `
\`\`\`
code here!
\`\`\``,
          icon: 'icon-code'
        }, {
          desc: '',
          template: `
1header 1 | header 2
---|---
text1 | text2
text1 | text2`,
          icon: 'icon-biaoge'
        }
      ]
    }
  },
  methods: {
    insert (temp, isSpecial = false) {
      if (!isSpecial) bus.$emit('insert', temp)
      else this[temp]()
    },
    link () {
      const _this = this
      this.$Modal.confirm({
        title: '',
        render: (h) => {
          return h('Form',
            [
              h('FormItem', {
                props: {
                  label: ':',
                }
              }, [
                  h('Input', {
                    props: { type: 'text', placeholder: '', maxlength: 10 },
                    on: {
                      input: e => {
                        this.linkText = e
                      }
                    }
                  })
                ]
              ),
              h('FormItem', {
                props: {
                  label: ':',
                }
              }, [
                  h('Input', {
                    props: { type: 'url', placeholder: '' },
                    on: {
                      input: e => {
                        this.linkAddress = e
                      }
                    }
                  })
                ]
              )
            ])
        },
        onOk () {
          bus.$emit('insert', `[${_this.linkText}](${_this.linkAddress})`)
        }
      })
    },
    makeFile () {
      if (!('download' in document.createElement('a'))) return this.$Notice.error({ title: '' })
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk () {
          bus.$emit('export')
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.header {
  height: 56px;
  line-height: 56px;
  border-bottom: 1px #ccc solid;
  box-shadow: 2px 2px 2px #ccc;
  .center {
    text-align: center;
  }
  .m_action_item {
    cursor: pointer;
    &:hover {
      color: rgb(11, 155, 212);
      font-weight: 900;
    }
  }
}
</style>




